import { Col, Row } from 'antd';
import React from 'react';
import gautam from '../../assets/PeopleImages/gautam.jpg';
import fb from '../../assets/Icons/facebook.png';
import li from '../../assets/Icons/linkedin.png';
import gm from '../../assets/Icons/gmail.png';
import tg from '../../assets/Icons/telegram.png';
import One from '../../assets/one.png';
import Two from '../../assets/two.png';
import Three from '../../assets/three.png';
import Learning from '../../assets/mission-learning.svg';
import Thinking from '../../assets/mission-critical-thinking.svg';
import Experts from '../../assets/mission-experts.svg';
//animation
import { motion } from 'framer-motion';
import { Helmet } from 'react-helmet';

const AboutGinnovation = () => {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className='about-common-page about-ginnovation'
    >
      <Helmet>
        <title>About Ginnovation Club</title>
        <meta
          name='description'
          content='Ginnovation club of GIET University, Gunupur is a step taken where students get a platform to present their ideas and innovative thoughts. The club helps these budding ideas by giving them a boost to convert ideas into invention of something appreciable and significant'
        />
      </Helmet>
      <div className='page-top ginnovation-top'>
        <div className='pattern'></div>
        <motion.header
          initial={{ y: 100, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.2, duration: 0.5 }}
        >
          <h2>About Ginnovation Club</h2>
          <p>The club that focous more on practicals</p>
        </motion.header>
      </div>
      <div className='container'>
        <div className='about'>
          <p className='about-text text-justify'>
            <strong>‘THE GINNOVATION CLUB’</strong> is an initiative of the
            Department of Electronics which exhibits the idea of providing a
            platform to young technocrats in order to intensify their knowledge.
            It focuses on the overall development of any student willing to put
            in hard labor for boundless experience. The club holds an open
            platform for all, without any discrimination and indulges in
            building a competitive environment which aims at enhancing the
            skills of students. It aims at teaching the students to have a
            practical approach and how they can apply the texts well-read in the
            real world. It provides the scholars an atmosphere which helps them
            develop critical thinking abilities which facilitates their creative
            activities. It seeks to bind the minds of young pupils from various
            fields as it strives to work on enriching the education imparted.
          </p>
        </div>
        <div className='founder'>
          <Row>
            <Col md={16}>
              <div className='content'>
                <h6 className='designation'>Ginnovation-Founder/Convener</h6>
                <h3>Mr. Gautam</h3>
                <h5>Asst. prof. ECE, GIETU</h5>
                <p className='about'>
                  Lorem Ipsum is simply dummy text of the printing and
                  typesetting industry. Lorem Ipsum has been the industry's
                  standard dummy text ever since the 1500s, when an unknown
                  printer took a galley of type and scrambled it to make a type
                  specimen book. It has survived not only five centuries, but
                  also the leap into electronic typesetting, remaining
                  essentially unchanged. It was popularised in the 1960s with
                  the release of Letraset sheets containing Lorem Ipsum
                  passages, and more recently with desktop publishing software
                  like Aldus PageMaker including versions of Lorem Ipsum.
                </p>
                <div className='sm-bar'>
                  <a
                    href='https://www.linkedin.com/in/gautam-kumar-151b576a/'
                    target='_blank'
                    rel='noreferrer'
                  >
                    <img src={li} alt='mr. gautam linkedin' />
                  </a>
                  <a
                    href='mailto:gautam@giet.edu'
                    target='_blank'
                    rel='noreferrer'
                  >
                    <img src={gm} alt='mr. gautam gmail' />
                  </a>
                  <a
                    href='https://www.facebook.com/gk2214397'
                    target='_blank'
                    rel='noreferrer'
                  >
                    <img src={tg} alt='mr. gautam telegram' />
                  </a>
                  <a
                    href='https://www.facebook.com/gk2214397'
                    target='_blank'
                    rel='noreferrer'
                  >
                    <img src={fb} alt='mr. gautam facebook' />
                  </a>
                </div>
              </div>
            </Col>
            <Col md={8}>
              <div className='image-container'>
                <img
                  src={gautam}
                  alt='ginnovation founder'
                  className='img-fluid'
                />
              </div>
            </Col>
          </Row>
        </div>
        <div className='mission'>
          <h2>Our Mission</h2>
          <Row gutter={12}>
            <Col md={8}>
              <div className='mission-card'>
                <img
                  src={Learning}
                  className='img-fluid'
                  alt='ginnovation missions'
                />
                <p>
                  Educating young scholars to operate efficiently with real life
                  problems and their solutions.
                </p>
              </div>
            </Col>
            <Col md={8}>
              <div className='mission-card'>
                <img
                  src={Thinking}
                  className='img-fluid'
                  alt='ginnovation missions'
                />
                <p>
                  Crafting the minds of students in order to enhance their
                  critical thinking abilities.
                </p>
              </div>
            </Col>
            <Col md={8}>
              <div className='mission-card'>
                <img
                  src={Experts}
                  className='img-fluid'
                  alt='ginnovation missions'
                />
                <p>
                  Yielding technocrats with profound knowledge, who expertise in
                  their area of concern.
                </p>
              </div>
            </Col>
          </Row>
        </div>
        <div className='mission vission'>
          <h2>Our Vission</h2>
          <div className='vission-card-container'>
            <div
              data-aos='fade-left'
              data-aos-duration='600'
              data-aos-delay='100'
              className='card'
            >
              <img src={One} className='img-fluid' alt='ginnovation vissions' />
              <span>
                Educating young scholars to operate efficiently with real life
                problems and their solutions.
              </span>
            </div>
            <div
              data-aos='fade-right'
              data-aos-duration='600'
              data-aos-delay='200'
              className='card'
            >
              <span>
                Educating young scholars to operate efficiently with real life
                problems and their solutions.
              </span>
              <img src={Two} className='img-fluid' alt='ginnovation vissions' />
            </div>
            <div
              data-aos='fade-left'
              data-aos-duration='600'
              data-aos-delay='100'
              className='card'
            >
              <img
                src={Three}
                className='img-fluid'
                alt='ginnovation vissions'
              />
              <span>
                Educating young scholars to operate efficiently with real life
                problems and their solutions.
              </span>
            </div>
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default AboutGinnovation;
